<template>
  <div class="imgTextCell">
    <div class="flx-container">
      <img src="img" class="icorn"/>
      <span class="content">{{content}}</span>
      <img class="arrow" src=""/>
    </div>
    <div class="bottom-line" v-show="!hidLine"></div>
  </div>
</template>

<script>
  export default {
    name: 'img-text-cell',
    props: ['img', 'content', 'hidLine'],
    data() {
      return {

      };
    },
  };
</script>

<style lang="less" scoped>
  @import "../style/common.less";
  .imgTextCell {
    position: relative;
  }
  .flx-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    .wh(7.1,0.9);
    .padding(0,0.2)
  }
  .icorn {
   .wh(0.6,0.6);
    background-color: yellow;
  }
  .content {
    .wh(5.6,0.6);
    background-color: yellowgreen;
  }
  .arrow {
    .wh(0.6,0.6);
    background-color: goldenrod;
  }
  .bottom-line {
    .wh(7.5,0.01);
    background-color: @line-color;
  }

</style>



