<template>
  <div>
    <div @click="navToNotice()"><imgTextCell content="消息公告" ></imgTextCell></div>
    <div @click="navToSystemNotice()">
      <imgTextCell  content="系统消息" hid-line="true" ></imgTextCell>
    </div>
  </div>
</template>

<script>
  import imgTextCell from '../../components/imgTextCell';
  import { navigateTo } from '../../utils/mRouter';

  export default {
    data() {
      return {

      };
    },
    components: { imgTextCell },
    methods: {
      navToNotice() {
        navigateTo('../msg_notice/msg_notice', 'msg_notice', { id: 123456 }, this);
      },
      navToSystemNotice() {
        navigateTo('../msg_systemNotice/msg_systemNotice', 'msg_systemNotice', { id: 123456 }, this);
      },
    },
  };
</script>

<style scoped>

</style>



